import { PDFDocument, rgb, StandardFonts } from "pdf-lib"
import * as pdfjsLib from "pdfjs-dist"

// Configure PDF.js worker
if (typeof window !== "undefined") {
  pdfjsLib.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjsLib.version}/pdf.worker.min.js`
}

export interface PDFToWordOptions {
  file: File
}

export interface PDFToImagesOptions {
  file: File
  format: "png" | "jpeg" | "webp"
  quality?: number
}

export interface CompressPDFOptions {
  file: File
}

export interface EncryptPDFOptions {
  file: File
  password: string
}

export interface MergePDFOptions {
  files: File[]
}

export class PDFService {
  static async extractTextFromPDF(file: File): Promise<string[]> {
    const arrayBuffer = await file.arrayBuffer()
    const pdf = await pdfjsLib.getDocument({ data: arrayBuffer }).promise
    const textPages: string[] = []

    for (let i = 1; i <= pdf.numPages; i++) {
      const page = await pdf.getPage(i)
      const textContent = await page.getTextContent()
      const pageText = textContent.items.map((item: any) => item.str).join(" ")
      textPages.push(pageText)
    }

    return textPages
  }

  static async convertToWord(options: PDFToWordOptions): Promise<Blob> {
    const { file } = options
    const textPages = await this.extractTextFromPDF(file)

    const pdfDoc = await PDFDocument.create()
    const font = await pdfDoc.embedFont(StandardFonts.Helvetica)
    const boldFont = await pdfDoc.embedFont(StandardFonts.HelveticaBold)

    textPages.forEach((text, index) => {
      const page = pdfDoc.addPage()
      const { height } = page.getSize()

      page.drawText(`Page ${index + 1}`, {
        x: 50,
        y: height - 50,
        size: 16,
        font: boldFont,
        color: rgb(0, 0, 0),
      })

      const lines = this.wrapText(text, 80)
      let yPosition = height - 80

      lines.forEach((line) => {
        if (yPosition < 50) return
        page.drawText(line, {
          x: 50,
          y: yPosition,
          size: 10,
          font,
          color: rgb(0, 0, 0),
        })
        yPosition -= 15
      })
    })

    const pdfBytes = await pdfDoc.save()
    return new Blob([pdfBytes], { type: "application/pdf" })
  }

  static async convertToImages(options: PDFToImagesOptions): Promise<Blob[]> {
    const { file, format, quality = 0.92 } = options
    const arrayBuffer = await file.arrayBuffer()
    const pdf = await pdfjsLib.getDocument({ data: arrayBuffer }).promise
    const images: Blob[] = []

    for (let i = 1; i <= pdf.numPages; i++) {
      const page = await pdf.getPage(i)
      const viewport = page.getViewport({ scale: 2.0 })

      const canvas = document.createElement("canvas")
      const context = canvas.getContext("2d")
      if (!context) continue

      canvas.height = viewport.height
      canvas.width = viewport.width

      await page.render({
        canvasContext: context,
        viewport: viewport,
      }).promise

      const blob = await new Promise<Blob>((resolve) => {
        canvas.toBlob((blob) => resolve(blob!), `image/${format}`, quality)
      })

      images.push(blob)
    }

    return images
  }

  static async compress(options: CompressPDFOptions): Promise<Blob> {
    const { file } = options
    const arrayBuffer = await file.arrayBuffer()
    const pdfDoc = await PDFDocument.load(arrayBuffer)

    const compressedPdfBytes = await pdfDoc.save({
      useObjectStreams: true,
      addDefaultPage: false,
    })

    return new Blob([compressedPdfBytes], { type: "application/pdf" })
  }

  static async encrypt(options: EncryptPDFOptions): Promise<Blob> {
    const { file, password } = options
    const arrayBuffer = await file.arrayBuffer()
    const pdfDoc = await PDFDocument.load(arrayBuffer)

    const encryptedPdfBytes = await pdfDoc.save({
      userPassword: password,
      ownerPassword: password,
    })

    return new Blob([encryptedPdfBytes], { type: "application/pdf" })
  }

  static async merge(options: MergePDFOptions): Promise<Blob> {
    const { files } = options
    const mergedPdf = await PDFDocument.create()

    for (const file of files) {
      const arrayBuffer = await file.arrayBuffer()
      const pdf = await PDFDocument.load(arrayBuffer)
      const copiedPages = await mergedPdf.copyPages(pdf, pdf.getPageIndices())
      copiedPages.forEach((page) => mergedPdf.addPage(page))
    }

    const mergedPdfBytes = await mergedPdf.save()
    return new Blob([mergedPdfBytes], { type: "application/pdf" })
  }

  private static wrapText(text: string, maxLength: number): string[] {
    const words = text.split(" ")
    const lines: string[] = []
    let currentLine = ""

    words.forEach((word) => {
      if ((currentLine + word).length <= maxLength) {
        currentLine += (currentLine ? " " : "") + word
      } else {
        if (currentLine) lines.push(currentLine)
        currentLine = word
      }
    })

    if (currentLine) lines.push(currentLine)
    return lines
  }
}
